<template>
  <div id="login-page">
    <h1>用户登录</h1>
    <div>
      <el-radio-group class="radio-group" v-model="character">
        <el-radio-button class="radio-button" label="学生"></el-radio-button>
        <el-radio-button class="radio-button" label="导师"></el-radio-button>
        <el-radio-button class="radio-button" label="上级"></el-radio-button>
        <el-radio-button class="radio-button" label="管理员"></el-radio-button>
      </el-radio-group>
    </div>
    <div class="login-form-div">
      <el-form
        class="login-form"
        ref="authForm"
        :model="auth"
        :label-position="labelPosition"
        :rules="rules"
        label-width="80px"
        size="medium">
        <el-form-item label="用户名" prop="username">
          <el-input v-model="auth.username" maxlength="20"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input v-model="auth.password" type="password" minlength="6" maxlength="20"></el-input>
        </el-form-item>
        <div class="flex-row">
        <el-form-item class="left-align">
          <el-checkbox v-model="rememberUsername" disabled>记住用户名</el-checkbox>
        </el-form-item>
        <a class="justify" href="javascript:void()">忘记密码？</a>
        </div>
        <el-form-item>
          <el-button class="submit-button" type="primary" @click="submitForm('authForm')">登录</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

function checkPasswordLength(password) {
  const length = password.length;
  return length >= 8 && length <= 20;
}

function postAuthData(authData) {
  axios.post('/auth', authData)
    .then((response) => {
      alert(response)
    })
    .catch((error) => {
      console.log(error)
    });
}

export default {
  name: 'Login',
  data () {
    return {
      labelPosition: 'right',
      character: '学生',
      rememberUsername: false,
      auth: {
        username: '',
        password: ''
      },
      rules: {
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        let result;
        if (!valid) {
          alert('form not complete');
          result = false;
        } else if (!checkPasswordLength(this.auth.password)) {
          alert('password length must be in [8, 20]');
          result = false;
        } else {
          alert('submit');
          postAuthData({
            character: this.character,
            username: this.auth.username,
            password: this.auth.password
          });
        }
        this.auth.password = '';
        return result;
      });
    }
  }
}
</script>

<style>
#login-page {
  width: 100%;
  height: 100%;
}
.login-form-div {
  margin: auto;
  margin-top: 32px;
  width: 400px;
}
.login-form {
  margin-left: -40px;
  margin-right: 40px;
}
.submit-button {
  width: 150px;
  font-size: 1.2em;
  font-weight: 500;
}
.el-radio-button__inner {
  font-size: 17px;
}
.left-align {
  text-align: left;
}
.flex-row {
  display: flex;
  flex-direction: row;
}
a {
  text-decoration: none;
  color: #4488BB;
  font-size: 14px;
}
.justify {
  margin-left: 156px;
  margin-top: 6px;
}
</style>



