// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import { Button
  , Checkbox
  , Form
  , FormItem
  , Input
  , RadioButton
  , RadioGroup
  } from 'element-ui'
import '../theme/index.css'
import App from './App'
import router from './router'

Vue.config.productionTip = false

Vue.use(Button)
Vue.use(Checkbox)
Vue.use(Input)
Vue.use(Form)
Vue.use(FormItem)
Vue.use(RadioButton)
Vue.use(RadioGroup)

Vue.component('user-tuple', {
  props: ['user'],
  template: '<li><tr><td>{{user.id}}</td><td>{{user.name}}</td><td>{{user.password}}</td></tr></li>'
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  // render: h => h(App),
  router,
  components: { App },
  template: '<App/>',
})



